.accordion-tabs {
  $base-border-color: #e8e6df !default;
  $base-border-radius: 3px !default;
  $base-background-color: #fff !default;
  $base-spacing: 1.5em !default;
  $action-color: #477dca !default;
  $dark-gray: $gray-dark !default;
  $light-gray: $gray-light !default;
  $medium-screen: em(640) !default;
  $tab-border: 0px solid $base-border-color;
  $tab-content-background: lighten($light-gray, 10);
  $tab-active-background: $tab-content-background;
  $tab-inactive-color: $base-background-color;
  $tab-inactive-hover-color: darken($light-gray, 5);
  $tab-mode: $bp-large;

  @include clearfix;
  line-height: 1.5;
  margin-bottom: $base-spacing;
  margin-left: 0;
  padding: 0;

  @include breakpoint(max-width $tab-mode) {
    border: $tab-border;
    border-radius: $base-border-radius;
  }

  .tab-header-and-content {
    list-style: none;

    @include breakpoint($tab-mode) {
      display: inline;
    }

    &:first-child .tab-link {
      border-top-left-radius: $base-border-radius;
      border-top-right-radius: $base-border-radius;

      @include breakpoint(max-width $tab-mode) {
        border-top: 0;
      }
    }

    &:last-child .tab-link {
      @include breakpoint(max-width $tab-mode) {
        border-bottom-left-radius: $base-border-radius;
        border-bottom-right-radius: $base-border-radius;
      }
    }
  }

  .tab-link {
    background-color: $tab-inactive-color;
    border-top: $tab-border;
    cursor: pointer;
    display: block;
    font-size: 1.2em;
    font-weight: bold;
    margin: 0;
    padding: 1em; // $base-spacing/2 $gutter/2;
    text-decoration: none;

    @include breakpoint($tab-mode) {
      border-top: 0;
      border-top-left-radius: $base-border-radius;
      border-top-right-radius: $base-border-radius;
      display: inline-block;
    }

    a {
      color: $dark-gray;
      text-decoration: none;
    }

    &:hover {
      color: $action-color;
    }

    &:focus {
      outline: none;
    }

    &.is-active {
      background-color: $tab-active-background;

      @include breakpoint($tab-mode) {
        background-color: $tab-active-background;
        border: $tab-border;
        border-bottom-color: $tab-active-background;
        margin-bottom: -1px;
      }
    }
  }

  .tab-content {
    background: $tab-content-background;
    display: none;
    padding: 2em; //$base-spacing $gutter;
    width: 100%;

    @include breakpoint($tab-mode) {
      border: $tab-border;
      border-bottom-left-radius: $base-border-radius;
      border-bottom-right-radius: $base-border-radius;
      border-top-right-radius: $base-border-radius;
      float: left;
    }
  }
}

.three_six_three-region--middle ul.accordion-tabs {
  margin-left: 0;
}
